import {
  JwtVerifiedCredentialFormatEnum,
  type SdkUser,
  WalletProviderEnum,
} from '@dynamic-labs/sdk-api-core';

export const userHasExternalWallet = (user: SdkUser) => {
  const hasExternalWallet = user?.verifiedCredentials.some(
    (vc) =>
      vc.format === JwtVerifiedCredentialFormatEnum.Blockchain &&
      vc.walletProvider !== WalletProviderEnum.EmbeddedWallet
  );

  return hasExternalWallet;
};
