import { getDefaultClient } from '../../../client/defaultClient';
import { assertDefined } from '../../../utils/assertDefined';
import type { Chain } from '../../chain';
import { userHasExternalWallet } from '../../wallets/userHasExternalWallet';
import { getChainsMissingWaasWalletAccounts } from '../getChainsMissingWaasWalletAccounts';

type ShouldAutoCreateWalletForChainParams = {
  chain: Chain;
};

/**
 * Checks if a WaaS wallet should be automatically created for the specified blockchain.
 *
 * This function determines whether the current user configuration and project
 * settings require automatic wallet creation for the given chain, considering
 * whether the user already has an embedded wallet for the enabled chains or an external wallet.
 *
 * @param params.chain - The blockchain network to check.
 * @param [client] - The Dynamic client instance. Only required when using multiple Dynamic clients.
 * @returns True if a wallet should be auto-created for the chain, false otherwise.
 */
export const shouldAutoCreateWalletForChain = (
  { chain }: ShouldAutoCreateWalletForChainParams,
  client = getDefaultClient()
) => {
  assertDefined(client.projectSettings, 'Project settings are not available');

  const embeddedWalletSettings = client.projectSettings.sdk.embeddedWallets;

  const automaticEmbeddedWalletCreationEnabled =
    embeddedWalletSettings?.automaticEmbeddedWalletCreation;

  if (!automaticEmbeddedWalletCreationEnabled || !client.user) {
    return false;
  }

  const automaticEmbeddedWalletCreationForExternalEnabled =
    embeddedWalletSettings?.automaticEmbeddedWalletCreationForExternal;

  // If user has an external wallet (e.g metamask, phantom, etc), should only
  // create if automaticEmbeddedWalletCreationForExternalEnabled is true
  const shouldCreateDynamicWaasWallet =
    !userHasExternalWallet(client.user) ||
    automaticEmbeddedWalletCreationForExternalEnabled;

  if (!shouldCreateDynamicWaasWallet) {
    return false;
  }

  const chainsMissingAutoCreatedWallet =
    getChainsMissingWaasWalletAccounts(client);

  return chainsMissingAutoCreatedWallet.includes(chain);
};
