import { WalletProviderEnum } from '@dynamic-labs/sdk-api-core';

import type { DynamicClient } from '../../../client/types/DynamicClient';
import { NoWalletProviderFoundError } from '../../../errors/NoWalletProviderFoundError';
import type { Chain } from '../../chain';
import { formatWalletProviderKey } from '../../wallets/utils/formatWalletProviderKey';
import { DYNAMIC_WAAS_METADATA } from '../constants';
import type { WaasWalletProvider } from '../waas.types';
import { findWaasWalletProviderByChain } from '../findWaasWalletProviderByChain/findWaasWalletProviderByChain';

type GetWaasWalletProviderByChainParams = {
  chain: Chain;
};

export const getWaasWalletProviderByChain = (
  { chain }: GetWaasWalletProviderByChainParams,
  client: DynamicClient
): WaasWalletProvider => {
  const waasProvider = findWaasWalletProviderByChain({ chain }, client);

  if (!waasProvider) {
    const waasProviderKey = formatWalletProviderKey({
      chain,
      displayName: DYNAMIC_WAAS_METADATA.displayName,
      walletProviderType: WalletProviderEnum.EmbeddedWallet,
    });

    throw new NoWalletProviderFoundError({
      walletProviderKey: waasProviderKey,
    });
  }

  return waasProvider;
};
