import type { ThresholdSignatureScheme } from '@dynamic-labs-wallet/browser-wallet-client';
import { WalletProviderEnum } from '@dynamic-labs/sdk-api-core';

import { getDefaultClient } from '../../../client/defaultClient';
import { NotWaasWalletProviderError } from '../../../errors/NotWaasWalletProviderError';
import { NoWalletProviderFoundError } from '../../../errors/NoWalletProviderFoundError';
import { filterDuplicates } from '../../../utils/filterDuplicates';
import type { Chain } from '../../chain';
import { refreshAuth } from '../../user/refreshAuth';
import { getWalletProviderByKey } from '../../wallets/getWalletProviderByKey';
import { formatWalletProviderKey } from '../../wallets/utils/formatWalletProviderKey';
import { DYNAMIC_WAAS_METADATA } from '../constants';
import { isWaasWalletProvider } from '../isWaasWalletProvider';

type CreateWaasWalletAccountsParams = {
  /**
   * For which chains to create wallet accounts.
   * If a chain is passed multiple times, just as many wallet accounts will be created
   * for that chain.
   */
  chains: Chain[];
  /**
   * The optional password to use for the wallet accounts.
   * It's used to encrypt the user shares.
   * The same password must be used for all waas wallet accounts created for the same user.
   */
  password?: string;
  /**
   * The threshold signature scheme to use for the wallet accounts.
   * If not provided, TWO_OF_TWO will be used.
   */
  thresholdSignatureScheme?: ThresholdSignatureScheme;
};

/**
 * Creates wallet accounts for the specified chains.
 *
 * This function creates wallet accounts for the specified chains.
 * If the same chain is passed multiple times, just as many wallet accounts will be created
 * for that chain.
 *
 * @example
 * ```ts
 * // Will create 2 wallet accounts for EVM and 1 for SOL
 * await createWaasWalletAccounts({ chains: ['EVM', 'EVM', 'SOL'] });
 * ```
 *
 * @param params.chains - The chains to create wallet accounts for.
 * @param params.password - The optional password to use for the wallet accounts.
 * @param params.thresholdSignatureScheme - The threshold signature scheme to use for the wallet accounts. (Default: TWO_OF_TWO)
 * @param [client] - The Dynamic client instance. Only required when using multiple Dynamic clients.
 * @returns A promise that resolves when all wallet accounts are created.
 */
export const createWaasWalletAccounts = async (
  {
    chains,
    password,
    thresholdSignatureScheme,
  }: CreateWaasWalletAccountsParams,
  client = getDefaultClient()
): Promise<void> => {
  // we need this right now so we don't try to create wallet accounts for chains
  // that we don't support Dynamid Waas yet in the JS SDK, like BTC
  const supportedChains = ['EVM', 'SOL', 'SUI'];
  const filteredSupportedChains = chains.filter((chain) =>
    supportedChains.includes(chain)
  );

  if (filteredSupportedChains.length === 0) {
    return;
  }

  const walletProviderKeys = filteredSupportedChains.map((chain) =>
    formatWalletProviderKey({
      chain,
      displayName: DYNAMIC_WAAS_METADATA.displayName,
      walletProviderType: WalletProviderEnum.EmbeddedWallet,
    })
  );

  const walletProviderEntries = filterDuplicates(walletProviderKeys).map(
    (walletProviderKey) => {
      const walletProvider = getWalletProviderByKey(
        { walletProviderKey },
        client
      );

      if (!walletProvider) {
        throw new NoWalletProviderFoundError({
          walletProviderKey,
        });
      }

      if (!isWaasWalletProvider(walletProvider)) {
        throw new NotWaasWalletProviderError({
          walletProviderKey,
        });
      }

      return [walletProviderKey, walletProvider] as const;
    }
  );

  const walletProviderMap = Object.fromEntries(walletProviderEntries);

  await Promise.all(
    walletProviderKeys.map((walletProviderKey) =>
      walletProviderMap[walletProviderKey].createWalletAccount({
        password,
        thresholdSignatureScheme,
      })
    )
  );

  await refreshAuth(client);
};
